
.contact .row{
    background-color:cadetblue;
	max-width: 400px;
	margin: 20px auto;
	padding: 10px 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-radius: 20px;
}
.contact :hover{
	box-shadow: 5px 5px 5px grey;
	opacity: 9;
}
.contact .row .rightside a {
	background: red;
	display: inline-block;
	padding: 10px 30px;
	border-radius: 50%;
	border: 1px solid rgba(255, 255, 255, 0.4);
}
.contact .row .rightside .phone {
	background-color: rgb(0, 181, 222);
	margin-right: 10px;
}

.contact .row .rightside a.whatsapp {
	background-color: rgb(0, 220, 99);
}

.contact .row .rightside a.mail {
	background-color: rgb(232, 186, 1);
}
